<template>
  <div class="layout-wrapper">
    <navigator section="edit"></navigator>
    <div class="edit-page">
      <div class="main-content">
        <div class="paper-wrapper">
          <paper-header
            v-bind:title="paper.title"
            v-bind:description="paper.description"
            v-on:paperHeaderChange="paperHeaderChange"
          >
          </paper-header>
          <div
            class="topic-wrapper"
            v-for="(topic, index) in paper.topics"
          >
            <edit-topic
              v-if="topicEditingID === topic.id"
              v-bind:topic="topic"
              v-on:savePaperTopic="savePaperTopic"
              v-on:cancelTopicEditing="cancelTopicEditing"
            >
            </edit-topic>
            <single-choice
              v-else
              v-bind:topic="topic"
              v-bind:topicIndex="index"
              v-on:switchToEdit="switchToEdit"
            >
            </single-choice>
            <span class="delete-topic" v-on:click="deletePaperTopic(topic.id)">&times;</span>
          </div>
          <div class="add-wrapper" v-if="topicAdding">
            <edit-topic
              v-on:savePaperTopic="savePaperTopic"
              v-on:cancelTopicEditing="cancelTopicEditing"
            >
            </edit-topic>
          </div>
          <add-topic-button v-on:addPaperTopic="addPaperTopic"></add-topic-button>
        </div>
      </div>
    </div>
    </div>
</template>

<script>
import _ from 'lodash'
import axios from 'axios'
import io from 'socket.io-client'
import { Changeset } from 'changesets'
import DmpMod from 'diff_match_patch'
import { remotePath } from '@/constants/RemotePath.js'
import Navigator from '@/layouts/commons/Navigator.vue'
import EditTopic from '@/components/papers/EditTopic.vue'
import AddTopicButton from '@/components/papers/AddTopicButton.vue'
import PaperHeader from '@/components/papers/PaperHeader.vue'
import SingleChoice from '@/components/papers/SingleChoice.vue'
import { newPaperDefault } from '@/constants/Topics.js'
export default {
  name: 'edit',
  components: {
    Navigator,
    EditTopic,
    AddTopicButton,
    PaperHeader,
    SingleChoice
  },
  data () {
    return {
      socket: io.connect(`${remotePath.socketHost}`, {forceNew: true}),
      id: this.$route.params.id,
      topicAdding: false,
      topicEditingID: null,
      isNew: this.$route.params.id === undefined,
      paper: {
        title: newPaperDefault.title,
        description: newPaperDefault.description,
        topics: []
      },
      originPaper: {
        title: newPaperDefault.title,
        description: newPaperDefault.description,
        topics: []
      }
    }
  },
  mounted () {
    if (!this.isNew) {
      this.fetchPaper()
    }
    this.socket.on('updateSuccess', (res) => {
      console.log('received updated message:', res)
      if (res.data.status) {
        if (this.id === res.data.paper._id) {
          const computedResult = this.getComputedResult(res.data.paper)
          this.paper = computedResult
        }
      }
    })
  },
  methods: {
    stringifyJSON (data) {
      if (data) {
        const dataCp = _.cloneDeep(data)
        return JSON.stringify(dataCp)
      }
    },
    parseJSON (data) {
      if (data) {
        const dataCp = _.cloneDeep(data)
        return JSON.parse(dataCp)
      }
    },
    computedChanges (text, textNew) {
      const dmp = new DmpMod.diff_match_patch()
      const diff = dmp.diff_main(text, textNew)
      const changeset = Changeset.fromDiff(diff)
      return changeset
    },
    getComputedResult (serverPaper) {
      const originPaperText = this.stringifyJSON(_.cloneDeep(this.originPaper))
      const serverPaperText = this.stringifyJSON(_.cloneDeep(serverPaper))
      const editingPaperText = this.stringifyJSON(_.cloneDeep(this.paper))
      const csA = this.computedChanges(originPaperText, editingPaperText)
      const csB = this.computedChanges(originPaperText, serverPaperText)
      const transformsCsA = csA.transformAgainst(csB)
      return this.parseJSON(transformsCsA.apply(serverPaperText))
    },
    fetchPaper () {
      const url = `${remotePath.path}/paper/getPaper?id=${this.id}`
      axios.get(url)
        .then((response) => {
          this.paper = response.data.data.paper
        }).catch((err) => {
          alert(err)
        })
    },
    addPaperTopic () {
      this.topicAdding = true
    },
    switchToEdit (id) {
      this.topicEditingID = id
    },
    savePaperTopic (topic) {
      if (this.topicAdding) {
        this.paper.topics.push(topic)
      } else {
        let topicIndex
        this.paper.topics.filter((item, index) => {
          if (item.id === topic.id) {
            topicIndex = index
            return true
          }
        })
        this.paper.topics.splice(topicIndex, 1, topic)
      }
      this.savePaper()
    },
    deletePaperTopic (topic) {
      let topicIndex
      this.paper.topics.filter((item, index) => {
        if (item.id === topic.id) {
          topicIndex = index
          return true
        }
      })
      this.paper.topics.splice(topicIndex, 1)
      this.savePaper()
    },
    cancelTopicEditing () {
      console.log('cancel')
      this.topicAdding = false
      this.topicEditingID = null
    },
    paperHeaderChange (title, description) {
      this.paper.title = title
      this.paper.description = description
      this.savePaper()
    },
    createPaper () {
      return axios.post(`${remotePath.path}/paper/createPaper`)
    },
    updatePaper (paperJSON) {
      console.log(paperJSON)
      axios({
        method: 'post',
        url: `${remotePath.path}/paper/updatePaper`,
        headers: {
          'Content-Type': 'application/json'
        },
        data: paperJSON
      }).then((response) => {
        this.topicAdding = false
        this.topicEditingID = null
        this.originPaper = _.cloneDeep(this.paper)
        if (!this.isNew) {
          this.socket.emit('updatePaper', {id: this.id})
        }
        this.isNew = false
      }).catch((err) => {
        alert(err)
      })
    },
    savePaper () {
      if (this.isNew) {
        this.createPaper()
          .then((response) => {
            console.log(response)
            const respData = response.data
            this.$router.push({name: 'edit', params: {id: respData.data.id}})
            this.id = respData.data.id
            this.paper.id = respData.data.id
            const paperJSON = this.stringifyJSON(this.paper)
            this.updatePaper(paperJSON)
          })
      } else {
        const paperJSON = this.stringifyJSON(this.paper)
        this.updatePaper(paperJSON)
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .topic-wrapper {
    position: relative;
    padding: 30px 0;
    border-bottom: 1px solid #eee;
    .delete-topic {
      position: absolute;
      top: 0;
      right: 0;
      display: none;
      font-size: 24px;
      color: #333;
      cursor: pointer;
    }
    &:hover {
      .delete-topic {
        display: block;
      }
    }
  }
</style>



